<template>
	<image 
	:src="src" 
	:mode="mode" 
	:lazy-load="lazyLoad" 
	:fade-show="fadeShow" 
	:webp="webp"
	:show-menu-by-longpress="showMenuByLongpress"
	:draggable="draggable"
	:style="{
		width:getWidth,
		height:getHeight
	}"
	:class="[isRund ? 'view-imgae':'',]"
	></image>
</template>

<script>
	export default {
		props:{
			// 原生事件
			src:{
				deafult:""
			},
			mode:{
				default:"scaleToFill"
			},
			lazyLoad:{
				default:false
			},
			fadeShow:{
				default:false
			},
			webp:{
				default:false
			},
			showMenuByLongpress:{
				default:false
			},
			draggable:{
				default:true
			},
			// 新增属性 是否圆角
			round:{
				type: Boolean | String,
				default:false
			},
			width:{
				type:String | Number,
			},
			height:{
				type:String | Number
			}
		},
		computed:{
			getWidth(){
				let str = Object.prototype.toString.call(this.width);
				
				if("[object Number]" == str) {
					return this.width+"rpx"
				}
				
				return this.width
			},
			getHeight(){
				let str = Object.prototype.toString.call(this.height);
				
				if("[object Number]" == str) {
					return this.height+"rpx"
				}
				return this.height
			},
			isRund(){
				let str = Object.prototype.toString.call(this.round);
				
				if("[object Boolean]" == str){
					return true
				}
				return false
			},
		},
	}
	
</script>

<style lang="scss">
	.view-imgae{
		border-radius: $uni-border-radius-base;
	}
	
</style>
